.formacion{
    width: 80vw;
    margin:4rem auto;

}
.formacion__titulo{
    color: var(--accent-secondary);
    margin: 2rem auto;
    font-weight: 500;
    letter-spacing: 1rem;
}
.formacion__contenedor{
width: 70%;
margin: 0 auto;
display: flex;
flex-direction: column;
gap: 1.2rem;
}
@media screen and (max-width:768px) {
    .formacion__contenedor{
        width: 90%;
    }
}

.experiencia__titulo{
 color: var(--accent-ligth);
 margin: 2rem auto;
 font-weight: 400;  
}
.experiencia__item{
    width: 90%;
    padding-bottom: 4rem;
    border-color: var(--clear-white);
    display: flex;
    gap: 4rem;
    justify-content: flex-start;
    border-left: 1px solid #585353;
    text-align:justify;
}
.educacion__titulo{
    color: var(--accent-main); 
    margin: 2rem auto; 
    font-weight: 400;
}
.educacion__item{
    width: 90%;
    padding-bottom: 4rem;
    border-color: var(--clear-white);
    display: flex;
    gap:10%;
    justify-content: flex-start;
    border-left: 1px solid #585353;
    text-align:justify;
}
/* .educacion__item:first-of-type{
    border-left:1px solid rgb(16 185 129);
} */

.formacion__educacion{
    width: 70%;
    display: flex;
    flex-direction: column;
    position: relative;
    align-items: center;
    margin: 0 auto;
}
@media screen and (max-width:768px) {
    .formacion__educacion{
        width: 90%;
    }
}
.formacion__experiencia{
    width: 70%;
    display: flex;
    flex-direction: column;
    position: relative;
    align-items: center;
    margin: 0 auto;
}
@media screen and (max-width:768px) {
    .formacion__experiencia{
        width: 90%;
    }
}
.nodo{
    position: absolute;
    outline-color: #000;
    background-color: rgb(16 185 129/var(--tw-bg-opacity));/*color verde tipo clank de ratchet and clank*/
    border-radius:100%;
    width: 1.6rem;
    height: 1.6rem;
    aspect-ratio: 1/1;
    left: -8px;
    top: -2px;
}
.nodo-educacion{
    background-color: rgb(247 129 102/var(--tw-bg-opacity));
}
.nodo-experiencia{
    background-color: rgb(123 184 209/var(--tw-bg-opacity));
}
.nodo-parpadeante{
    animation: ping 1s cubic-bezier(0,0,.2,1) infinite;
    width: 1.7rem;
    height: 1.7rem;
    aspect-ratio: 1/1;
    z-index: -10;
    top: -1.5px;
    left: -1px;
    position: absolute;
    color-scheme: light;
    border-color: rgb(38 38 38/var(--tw-border-opacity));
    border-radius: 9999px;
    /* background-color: rgb(16 185 129/var(--tw-bg-opacity)); */
}
.nodo-previo{
    position: absolute;
    outline-color: #000;
    background-color: #585353;
    border-radius:100%;
    width: 1.6rem;
    height: 1.6rem;
    aspect-ratio: 1/1;
    left: -8px;
    top: -2px;

}

@keyframes ping {
    75%, 100% {
        transform: scale(2);
        opacity: 0;
    }
}
.formacion__parrafo{
    width: 90%;
    height: auto;
    overflow: hidden;
    text-align: start;
}